import { ref } from '@vue/reactivity';
import { onMounted } from '@vue/runtime-core';
export default {
  name: 'MyCarousel',
  setup() {
    // 获取当前显示的元素
    let activeDiv = ref();
    let moving = false; // 节流
    let step = 600; // 移动的步长
    let time = 200; // 完成动画的总时间(ms) 
    let k = 100; // 移动的次数【决定动画的移动丝滑程度】 需要能整除step和time
    // 左右两个按钮的动画定时器
    // let timer_left = {one: null, two: null};
    // let timer_right = {one: null, two: null};
    let timer_left = null;
    let timer_right = null;
    let move_offset = 45; // 左右按钮的动画偏移量
    let move_time = 10; // 左右按钮的动画执行时间
    let j = 100; // 左右按钮每次移动的次数
    // 自动移动部分
    let timer_auto = null; // 自动播放的定时器
    let flag = false; // 标记自动播放定时器 是否应该存在
    // 元素， 完成动画时间, 目标位置
    // 动画存在的问题：由于是逐个元素绑定的，所以可能出现元素间有缝隙的情况。
    // 解决方案：把两个元素的偏移写入一个定时器内，这样可以避免移动不同步 而出现的缝隙【懒得改了】
    // function myAnimate(element, time, target) {
    //     // console.log('parseFloat(element.style.left)', element.style.left);
    //     let moveStep = target-parseFloat(element.style.left);
    //     let onceStep = moveStep/k;
    //     let onceTime = time/k;
    //     let timer = setInterval(() => {
    //         let i = parseFloat(element.style.left);
    //         i+=onceStep;
    //         // 防止因为onceStep是除不尽小数，而导致i最终不等于target
    //         if(onceStep>0 && i>target || onceStep<0 && i<target) {
    //             i = target;
    //         }
    //         element.style.left = i + 'px';
    //         if(i === target) {
    //             clearInterval(timer);
    //             if(target === 0) {      // 最后移动的总是target为0
    //                 moving = false;
    //             }
    //         }
    //     }, onceTime);
    // }
    // 第一个元素，第一个元素的目标位置，第二个元素，第二个元素的目标位置，完成动画的总时间
    function myAnimate(first_ele, first_target, second_ele, second_target, time) {
      let first_moveStep = first_target - parseFloat(first_ele.style.left); // 计算第一个元素的移动距离
      let first_onceStep = first_moveStep / k; // 计算第一个元素的单次移动距离
      let second_moveStep = second_target - parseFloat(second_ele.style.left); // 计算第二个元素的移动距离
      let second_onceStep = second_moveStep / k; // 计算第二个元素的单次移动距离      
      let onceTime = time / k;
      let timer = setInterval(() => {
        // 进行第一个元素的偏移
        let i = parseFloat(first_ele.style.left);
        i += first_onceStep;
        // 由于单步步长 是 移动距离 / k ，所以需要在i超过【移动距离】的时候进行调整
        if (first_onceStep > 0 && i > first_target || first_onceStep < 0 && i < first_target) {
          i = first_target;
        }
        first_ele.style.left = i + 'px';
        // 进行第二个元素的偏移
        let t = parseFloat(second_ele.style.left);
        t += second_onceStep;
        // 由于单步步长 是 移动距离 / k  ，所以需要在t超过【移动距离】的时候进行调整
        if (second_onceStep > 0 && t > second_target || second_onceStep < 0 && t < second_target) {
          t = second_target;
        }
        second_ele.style.left = t + 'px';
        // 两个元素均到达目标位置
        if (i === first_target && t === second_target) {
          clearInterval(timer);
          moving = false;
          // 判断是否还需要自动播放
          if (timer_auto != null && flag === false) {
            clearInterval(timer_auto);
            timer_auto = null;
          }
        }
      }, onceTime);
    }
    function clickLeftBtn() {
      if (moving) {
        // 还在移动，就阻断行为，停止增加定时器
        return;
      }
      // if(timer_auto!=null && flag === false) {    // 自动定时器存在 且 标记其不应当存在时       
      //     clearInterval(timer_auto);
      //     timer_auto = null;
      // }
      moving = true;
      //移除左按钮的鼠标移入事件的定时器
      // clearInterval(timer_left.one);
      // clearInterval(timer_left.two);
      clearInterval(timer_left);
      // console.log('当前位于中心的元素是:', activeDiv.value);
      let previousElement = activeDiv.value.previousElementSibling;
      // console.log('上一个元素是:', previousElement);
      if (previousElement === null) {
        // console.log('没有上一个元素');
        // 获取最后一个元素
        previousElement = activeDiv.value.parentNode.children[activeDiv.value.parentNode.children.length - 1];
      }
      previousElement.style.left = -1 * step + 'px'; // 将上一个元素调整到左侧
      // myAnimate(activeDiv.value, time, step);
      // myAnimate(previousElement, time, 0);
      myAnimate(activeDiv.value, step, previousElement, 0, time);
      // 更新当前显示的元素
      activeDiv.value = previousElement;
    }
    function clickRightBtn() {
      if (moving) {
        // 还在移动，就阻断行为，停止增加定时器
        return;
      }
      // if(timer_auto!=null && flag === false) {    // 自动定时器存在 且 标记其不应当存在时       
      //     clearInterval(timer_auto);
      //     timer_auto=null;
      //     return;                                 
      //     // 这里返回是为了消除自动定时器是在本次调用clickRightBtn执行的，但是其实不应该执行后续偏移，所以返回
      //     // 出现的问题：如果在自动移动未完成先，移入鼠标，手动点按钮，则会导致自动定时器提前被清除，但是用户需要的效果无效
      // }
      console.log('右移!');
      moving = true;
      //移除右按钮的鼠标移入事件的定时器
      // clearInterval(timer_right.one);
      // clearInterval(timer_right.two);
      clearInterval(timer_right);
      // console.log('当前位于中心的元素是:', activeDiv.value);
      let nextElement = activeDiv.value.nextElementSibling;
      // console.log('下一个元素是: ', nextElement);
      if (nextElement === null) {
        // console.log('没有下一个');
        nextElement = activeDiv.value.parentNode.children[0]; // 获取第一个元素
      }

      nextElement.style.left = step + 'px'; // 将下一个元素调到右侧
      // myAnimate(activeDiv.value, time, -1*step);
      // myAnimate(nextElement, time, 0);
      myAnimate(activeDiv.value, -1 * step, nextElement, 0, time);
      // 更新当前显示的元素
      activeDiv.value = nextElement;
    }
    // 鼠标移入/移出按钮的偏移动画
    // function myAnimate_mouse(element, time, target) {
    //     // console.log('parseFloat(element.style.left)', element.style.left);
    //     let moveStep = target-parseFloat(element.style.left);
    //     let onceStep = moveStep/j;
    //     let onceTime = time/j;
    //     let timer = setInterval(() => {
    //         let i = parseFloat(element.style.left);
    //         i+=onceStep;
    //         // 防止因为onceStep是除不尽小数，而导致i最终不等于target
    //         if(onceStep>0 && i>target || onceStep<0 && i<target) {
    //             i = target;
    //         }
    //         element.style.left = i + 'px';
    //         if(i === target) {
    //             clearInterval(timer);
    //         }
    //     }, onceTime);
    //     return timer;           // 返回定时器
    // }
    function myAnimate_mouse(first_ele, first_target, second_ele, second_target, time) {
      let first_moveStep = first_target - parseFloat(first_ele.style.left);
      let first_onceStep = first_moveStep / j;
      let second_moveStep = second_target - parseFloat(second_ele.style.left);
      let second_onceStep = second_moveStep / j;
      // console.log('单次位移量', first_onceStep, second_onceStep);
      // console.log('目标位置:', first_target, second_target);
      let onceTime = time / j;
      let timer = setInterval(() => {
        // 进行第一个元素的偏移
        let i = parseFloat(first_ele.style.left);
        i += first_onceStep;
        // 当 超过 目标位置时，进行调整
        if (first_onceStep > 0 && i > first_target || first_onceStep < 0 && i < first_target) {
          i = first_target;
        }
        first_ele.style.left = i + 'px';
        // 进行第二个元素的偏移
        let t = parseFloat(second_ele.style.left);
        t += second_onceStep;
        // 当 超过 目标位置时，进行调整
        if (second_onceStep > 0 && t > second_target || second_onceStep < 0 && t < second_target) {
          t = second_target;
        }
        second_ele.style.left = t + 'px';
        // 两个元素都到达目标位置后停止
        if (i === first_target && t === second_target) {
          clearInterval(timer);
        }
      }, onceTime);
      return timer; // 返回定时器
    }

    function mouseenter_leftBtn() {
      if (moving) {
        return;
      }
      // 清除左按钮的鼠标移出定时器
      // clearInterval(timer_left.one);
      // clearInterval(timer_left.two);
      clearInterval(timer_left);
      clearInterval(timer_right);
      // 获取上一个元素
      let previousElement = activeDiv.value.previousElementSibling;
      if (previousElement === null) {
        // 获取最后一个元素
        previousElement = activeDiv.value.parentNode.children[activeDiv.value.parentNode.children.length - 1];
      }
      previousElement.style.left = -1 * step + 'px'; // 调整其位置到左侧
      // 计算目标位置
      // 左按钮，需要往回缩一点，是+
      // let target_pre = parseFloat(previousElement.style.left) + move_offset;
      // let target_now = parseFloat(activeDiv.value.style.left) + move_offset;
      let target_pre = -1 * step + move_offset;
      let target_now = 0 + move_offset;
      // timer_left.one = myAnimate_mouse(previousElement, move_time, target_pre);
      // timer_left.two = myAnimate_mouse(activeDiv.value, move_time, target_now);
      timer_left = myAnimate_mouse(previousElement, target_pre, activeDiv.value, target_now, move_time);
    }
    function mouseleave_leftBtn() {
      if (moving || parseInt(activeDiv.value.style.left) === 0) {
        // 正在执行点击事件 或者 活动的div在正中间  不用执行回位操作
        return;
      }
      // 清除左按钮的鼠标移入定时器
      // clearInterval(timer_left.one);
      // clearInterval(timer_left.two);
      clearInterval(timer_left);
      clearInterval(timer_right);
      // 获取目标元素
      let previousElement = activeDiv.value.previousElementSibling;
      if (previousElement === null) {
        // 获取最后一个元素
        previousElement = activeDiv.value.parentNode.children[activeDiv.value.parentNode.children.length - 1];
      }
      // timer_left.one = myAnimate_mouse(previousElement, move_time, -1*step);
      // timer_left.two = myAnimate_mouse(activeDiv.value, move_time, 0);
      timer_left = myAnimate_mouse(previousElement, -1 * step, activeDiv.value, 0, move_time);
    }
    function mouseenter_rightBtn() {
      if (moving) {
        return;
      }
      // 清除右按钮的鼠标移出定时器
      // clearInterval(timer_right.one);
      // clearInterval(timer_right.two);
      clearInterval(timer_left);
      clearInterval(timer_right);
      // 获取下一个元素
      let nextElement = activeDiv.value.nextElementSibling;
      if (nextElement === null) {
        nextElement = activeDiv.value.parentNode.children[0]; // 获取第一个元素
      }

      nextElement.style.left = step + 'px'; // 调整其位置到右侧
      // 计算目标位置
      // 右按钮，需要往左缩一点，是-
      // let target_now = parseFloat(activeDiv.value.style.left) - move_offset;
      // let target_next = parseFloat(nextElement.style.left) - move_offset;
      let target_now = 0 - move_offset;
      let target_next = step - move_offset;

      // timer_right.one = myAnimate_mouse(activeDiv.value, move_time, target_now);
      // timer_right.two = myAnimate_mouse(nextElement, move_time, target_next);
      timer_right = myAnimate_mouse(activeDiv.value, target_now, nextElement, target_next, move_time);
    }
    function mouseleave_rightBtn() {
      if (moving || parseInt(activeDiv.value.style.left) === 0) {
        // 正在执行点击事件 或者 活动的div在正中间  不用执行回位操作
        return;
      }
      // 清除右按钮的鼠标移入定时器
      // clearInterval(timer_right.one);
      // clearInterval(timer_right.two);
      clearInterval(timer_left);
      clearInterval(timer_right);
      // 获取目标元素
      let nextElement = activeDiv.value.nextElementSibling;
      if (nextElement === null) {
        nextElement = activeDiv.value.parentNode.children[0]; // 获取第一个元素
      }
      // timer_right.one = myAnimate_mouse(nextElement, move_time, step);
      // timer_right.two = myAnimate_mouse(activeDiv.value, move_time, 0);
      timer_right = myAnimate_mouse(nextElement, step, activeDiv.value, 0, move_time);
    }
    function autoPlay() {
      console.log('自动播放方法执行');
      clearInterval(timer_auto);
      timer_auto = setInterval(() => {
        clickRightBtn();
      }, 6000);
      flag = true;
    }
    function stopPlay() {
      console.log('停止自动播放');
      if (moving) {
        // 如果正在移动，那就先不取消此次动画，由下一次动画调用clickRightBtn内部移除该定时器
        flag = false;
        return;
      }
      clearInterval(timer_auto);
      timer_auto = null;
      flag = false;
    }
    onMounted(() => {
      autoPlay();
    });

    // 数据部分 
    let data = [[{
      id: 0,
      imgUrl: 'https://img.alicdn.com/imgextra/i4/O1CN01M4wDLz1plhvD5DiAV_!!6000000005401-2-tps-96-96.png_.webp',
      href: '#',
      text: '严选好物'
    }, {
      id: 1,
      imgUrl: 'https://img.alicdn.com/imgextra/i4/O1CN0134DxlN1RyU5gApXVd_!!6000000002180-2-tps-96-96.png_.webp',
      href: '#',
      text: '找新品'
    }, {
      id: 2,
      imgUrl: 'https://img.alicdn.com/imgextra/i2/O1CN016NXYvd1kaBmPUMJbZ_!!6000000004699-2-tps-96-96.png_.webp',
      href: '#',
      text: '淘宝专供'
    }, {
      id: 3,
      imgUrl: 'https://img.alicdn.com/imgextra/i4/O1CN01C5b1GL1EaaQ66ezkr_!!6000000000368-2-tps-96-96.png_.webp',
      href: '#',
      text: '跨境专供'
    }, {
      id: 4,
      imgUrl: 'https://img.alicdn.com/imgextra/i2/O1CN01dnw5ib21k7wdY7pIE_!!6000000007022-1-tps-96-96.gif',
      href: '#',
      text: '1688PLUS'
    }, {
      id: 5,
      imgUrl: 'https://gw.alicdn.com/imgextra/i2/O1CN01ESuulN26WuZnuDRUu_!!6000000007670-2-tps-96-96.png_.webp',
      href: '#',
      text: '天天特卖'
    }, {
      id: 6,
      imgUrl: 'https://img.alicdn.com/imgextra/i3/O1CN01SSk30Y1Vp6RaTfNLa_!!6000000002701-2-tps-96-96.png_.webp',
      href: '#',
      text: '实力优品'
    }], [{
      id: 0,
      imgUrl: 'https://img.alicdn.com/imgextra/i1/O1CN01FMXiRD1FMDc43Icax_!!6000000000472-2-tps-96-96.png_.webp',
      href: '#',
      text: '企业采购'
    }, {
      id: 1,
      imgUrl: 'https://img.alicdn.com/imgextra/i1/O1CN01jeDj8L1eHyKjWMBSQ_!!6000000003847-2-tps-96-96.png_.webp',
      href: '#',
      text: '商人社区'
    }, {
      id: 2,
      imgUrl: 'https://img.alicdn.com/imgextra/i4/O1CN0134DxlN1RyU5gApXVd_!!6000000002180-2-tps-96-96.png_.webp',
      href: '#',
      text: '找新品'
    }, {
      id: 3,
      imgUrl: 'https://img.alicdn.com/imgextra/i2/O1CN016NXYvd1kaBmPUMJbZ_!!6000000004699-2-tps-96-96.png_.webp',
      href: '#',
      text: '淘宝专供'
    }, {
      id: 4,
      imgUrl: 'https://img.alicdn.com/imgextra/i4/O1CN01C5b1GL1EaaQ66ezkr_!!6000000000368-2-tps-96-96.png_.webp',
      href: '#',
      text: '跨界专供'
    }, {
      id: 5,
      imgUrl: 'https://img.alicdn.com/imgextra/i4/O1CN01M4wDLz1plhvD5DiAV_!!6000000005401-2-tps-96-96.png_.webp',
      href: '#',
      text: '严选好物'
    }, {
      id: 6,
      imgUrl: 'https://img.alicdn.com/imgextra/i4/O1CN01eE7xSe1TS5PgG7s2K_!!6000000002380-0-tps-96-96.jpg_.webp',
      href: '#',
      text: '宝藏工厂'
    }]];
    return {
      activeDiv,
      clickLeftBtn,
      clickRightBtn,
      mouseenter_leftBtn,
      mouseleave_leftBtn,
      mouseenter_rightBtn,
      mouseleave_rightBtn,
      autoPlay,
      stopPlay,
      data
    };
  }
