import "core-js/modules/es.array.push.js";
import { onMounted, reactive, toRefs, ref, watch } from '@vue/runtime-core';
import { instance_Search } from '../plugin/axiosInstance';
export default {
  props: {
    // 传入一个active，其值有【findG，findF，findP】分别指代【找货源，找工厂，找工业品】
    active: String
  },
  setup(props, context) {
    // console.log('setup:', props);
    // console.log('context:', context);
    // 活动的页面
    let activePage = reactive({
      findG: {
        isActive: false,
        // “找产品”是否活动
        ph: '搜索 商品/供应商/求购',
        // 搜索框的占位符显示内容
        class: {
          bigRed: false
        }
      },
      findF: {
        isActive: false,
        // “找工厂”是否活动
        ph: '输入产品名/工厂名称/所在地/工艺等 开始找工厂',
        class: {
          bigRed: false
        }
      },
      findP: {
        isActive: false,
        // “找工业品”是否活动
        ph: '找五金/机械/电器 上1688工业品',
        class: {
          bigRed: false
        }
      }
    });
    // 搜索框占位符
    let ph = ref('');
    // 搜索框内的输入保存
    let keyWord = ref('');
    // 照相机图片
    let photoUrl = ref('https://img.alicdn.com/imgextra/i3/O1CN0116UWgv1ouZsMOIfA7_!!6000000005285-2-tps-60-52.png');
    // 最近搜索关键字【数组】
    let recent_keywords = reactive([]);
    // 热门搜索关键字
    let hot_keywords = reactive(['男', '男鞋', '女', '空气裤', '男童春秋装', '水光袜', '加绒卫裤男', '教师节气球', '洗衣液', '便利贴', '伴手礼']);
    // 工厂推荐相关数据【后续可以改为ajax获取，不过要往axiosInstance.js增加，代理服务器也得写写】
    let factoryData = reactive([{
      name: '大牌代工厂',
      imgUrl: 'https://gw.alicdn.com/imgextra/i1/O1CN01vPED2s1yxSbEv6ZRI_!!6000000006645-2-tps-32-32.png',
      factorys: ['京东代工', '无印良品代工', '小米代工', '中国移动代工', '361°代工', '南极人代工', '优衣库代工', '可口可乐代工', '周大福代工']
    }, {
      name: '源头产地工厂',
      imgUrl: 'https://gw.alicdn.com/imgextra/i3/O1CN01Q3YKsM1mcbAaaHlJD_!!6000000004975-2-tps-32-32.png',
      factorys: ['义乌小商品', '织里童装', '广州潮流女装', '深圳数码家电', '汕头内衣', '诸暨袜业', '杭州古风女装', '虎门时装']
    }, {
      name: '大家都在搜',
      imgUrl: 'https://gw.alicdn.com/imgextra/i1/O1CN01c66ZWu1JfEalB63ue_!!6000000001055-2-tps-32-32.png',
      factorys: ['男童衬衣', '雨伞大量批发', '哈伦牛仔裤', '内裤男批发', '水光袜', '中秋灯笼', '教师节气球', '洗衣液', '便利贴', '电动牙刷', '四件套']
    }]);
    // 是否显示搜索提示框
    let isShowTips = ref(false);
    // 搜索返回的结果
    let ObjResult = reactive({
      lastKeyWord: '',
      results: []
    });
    // 搜索返回框是否显示
    let isShowResults = ref(false);

    // 照相机图片：鼠标悬浮修改
    function hoverPhoto() {
      photoUrl.value = 'https://img.alicdn.com/imgextra/i1/O1CN01d9tkR81gu6XZcEWvQ_!!6000000004201-2-tps-60-52.png';
    }
    // 照相机图片：还原图片
    function restorePhoto() {
      photoUrl.value = 'https://img.alicdn.com/imgextra/i3/O1CN0116UWgv1ouZsMOIfA7_!!6000000005285-2-tps-60-52.png';
    }
    // 【搜索按钮点击事件】发送ajax请求，发送关键字(传入要搜索的关键字)
    function sendKeyword(keyword) {
      keyword = keyword.trim(); // 消除左右两侧的空格
      // 输入为空
      if (keyword === '') {
        alert('输入框为空【此处闪烁两次输入框】');
        return;
      }
      // alert('1、发送ajax请求【数据是keyword】,得到新页面后在新页签打开;\n2、将keyword保存到localStorage');
      //【搜索的关键字存储到浏览器中】
      // 判断新关键字在数组中是否已存在
      let isExit = false;
      let recentArr = JSON.parse(localStorage.getItem('Alibar-recentSearch-keyWord')) || [];
      recent_keywords.splice(0); // 删除所有元素
      for (let tmp of recentArr) {
        recent_keywords.push(tmp);
      }
      recent_keywords.forEach(element => {
        if (element === keyword) {
          isExit = true;
        }
      });
      // 已存在，不需要更新
      if (isExit === true) {
        return;
      }
      // 新的关键字补充到【最近搜索】数组内
      recent_keywords.push(keyword);
      // 把新的关键字数组放到本地存储内
      localStorage.setItem('Alibar-recentSearch-keyWord', JSON.stringify(recent_keywords));
    }
    // 搜索框点击事件
    function clickInput() {
      if (keyWord.value.length === 0) {
        // 输入框未输入内容
        isShowResults.value = false;
        isShowTips.value = true;
      } else {
        searchData();
      }
    }
    // 【输入框失去焦点】隐藏搜索提示框与搜索结果框
    function hideTip_Result() {
      // console.log('我失去焦点了！');
      isShowTips.value = false;
      isShowResults.value = false;
    }
    // 监视输入框输入，发送ajax请求获取数据
    let timer = null;
    // 延迟0.7s发送ajax请求【等待用户输入完全】
    function delaySearchData() {
      // console.log('timer:', timer);
      if (timer != null) {
        // console.log('timer != null');
        // 移除定时器
        clearTimeout(timer);
        timer = null;
      }
      // 无关键字，取消发送ajax请求
      if (keyWord.value.length === 0) {
        return;
      }
      timer = setTimeout(() => {
        // 发送ajax请求
        // console.log('输入:', keyWord.value);
        instance_Search({
          method: 'GET',
          url: '/Search/searchData',
          params: {
            key: keyWord.value
          }
        }).then(response => {
          // console.log('得到的响应体:', response.data);
          // 保存本次搜索的关键字
          ObjResult.lastKeyWord = keyWord.value;
          // 清空上一次的返回结果
          ObjResult.results = [];
          let keyLength = keyWord.value.length;
          let arr = response.data;
          // 把获取到的数据与关键字相同的前缀部分删去
          arr.forEach(element => {
            ObjResult.results.push(element.slice(keyLength));
          });
          // 隐藏搜索提示框
          isShowTips.value = false;
          // 显示搜索返回框
          isShowResults.value = true;
        }, reject => {
          // 没有得到对应的数据
          // 隐藏搜索提示框
          isShowTips.value = false;
          // 隐藏搜索返回框
          isShowResults.value = false;
        });
      }, 700);
    }
    // 立即发送ajax请求【输入框有内容时点击发送ajax请求】
    function searchData() {
      if (timer != null) {
        // console.log('timer != null');
        // 移除定时器
        clearTimeout(timer);
        timer = null;
      }
      // 无关键字，取消发送ajax请求
      if (keyWord.value.length === 0) {
        return;
      }
      instance_Search({
        method: 'GET',
        url: '/Search/searchData',
        params: {
          key: keyWord.value
        }
      }).then(response => {
        // console.log('得到的响应体:', response.data);
        // 保存本次搜索的关键字
        ObjResult.lastKeyWord = keyWord.value;
        // 清空上一次的返回结果
        ObjResult.results = [];
        let keyLength = keyWord.value.length;
        let arr = response.data;
        // 把获取到的数据与关键字相同的前缀部分删去
        arr.forEach(element => {
          ObjResult.results.push(element.slice(keyLength));
        });
        // 隐藏搜索提示框
        isShowTips.value = false;
        // 显示搜索返回框
        isShowResults.value = true;
      }, reject => {
        // 没有得到对应的数据
        // 隐藏搜索提示框
        isShowTips.value = false;
        // 隐藏搜索返回框
        isShowResults.value = false;
      });
    }
    // 延时触发失去焦点事件【确保点击事件可以成功执行】
    function delayBlur() {
      setTimeout(() => {
        hideTip_Result();
      }, 150);
    }
    // 关键字点击事件
    function clickKey(keyword) {
      // console.log('关键字点击了!', keyword);
      keyWord.value = keyword;
      sendKeyword(keyword);
    }

    // 组件挂载后
    onMounted(() => {
      // 判断活动的是“找货源”，“找工厂”，“找工业品”中的哪个，修改样式
      activePage[props.active].isActive = true;
      activePage[props.active].class.bigRed = true;
      // 修改搜索框的占位符
      ph.value = activePage[props.active].ph;
      // 触发事件，将ph的值传递给NewMainBox组件

      // 获取本地存储的最近搜索关键字数组
      let recentArr = JSON.parse(localStorage.getItem('Alibar-recentSearch-keyWord')) || [];
      for (let tmp of recentArr) {
        recent_keywords.push(tmp);
      }
    });
    return {
      ...toRefs(activePage),
      // 判断当前活动的是哪个页面
      ph,
      // 搜索框占位符                
      photoUrl,
      // 【照相机】路径                
      hoverPhoto,
      // 鼠标悬浮时的照相机                
      restorePhoto,
      // 还原未悬浮时的照相机                
      keyWord,
      // 关键字                
      sendKeyword,
      // 发送关键字事件                
      recent_keywords,
      // 保存在localStorage的最近搜索关键字                
      hot_keywords,
      // 热门搜索关键字
      factoryData,
      // 工厂推荐
      clickInput,
      // 搜索栏点击事件
      hideTip_Result,
      // 【输入框失去焦点】隐藏搜索提示框与搜索结果框
      isShowTips,
      // 是否显示搜索提示框
      delaySearchData,
      // 延迟发送ajax请求查找数据
      searchData,
      // 立即发送ajax请求查找数据
      ...toRefs(ObjResult),
      // 搜索返回的结果
      isShowResults,
      // 是否显示搜索返回框
      clickKey,
      // 关键字点击事件
      delayBlur // 延迟失去焦点事件
    };
  }
};