    // 将数字转为两位字符串 
    function numToStr(num) {
      if (typeof num !== 'number') {
        // 输入的不是number类型
        return '00';
      }
      let k = parseInt(num);
      if (k < 10) {
        return '0' + k;
      }
      return k.toString();
    }
