          product: ['只能垃圾桶', '寡糖其', '香薰器', '空气净化器', '饮水器、直饮']
        }]
      }, {
        name: '汽车用品',
        address: ['广州汽车内饰', '天台座垫脚垫', '深圳影音导航'],
        classify: [{
          kind: '汽车内饰',
          product: ['车用钥匙包', '汽车腰靠', '挂件摆件', '遮阳挡', '头枕', '车载储物']
        }, {
          kind: '安全自驾',
          product: ['车锁', '自家装备', '安全座椅', '防盗器', '车载充气床', '防刮条']
        }, {
          kind: '维修保养',
          product: ['冷媒', '车模', '汽油添加剂', '防刮条', '大灯', '改装配件']
        }]
      }],
      isActive: false
    }]);
    // 【模拟】判断品类市场信息是否返回(不想命名了，用数组表示)
    let marketLoading = reactive([]);
    // 【模拟】品类市场信息返回————定时器
    let marketLoading_timer = [];
    // 轮播图
    let carouselData = reactive([{
      imgUrl: 'https://img.alicdn.com/imgextra/i2/O1CN01oVehzx23YKyuecaiY_!!6000000007267-0-tps-600-256.jpg_600x600q90.jpg_.webp',
      href: '#'
    }, {
      imgUrl: 'https://img.alicdn.com/imgextra/i3/630625371/O1CN01T7iHIV1pXxyc2HDNd_!!630625371-0-wxb1688.jpg_600x600q90.jpg_.webp',
      href: '#'
    }, {
      imgUrl: 'https://img.alicdn.com/imgextra/i1/630625371/O1CN01vQYBbg1pXxyfprQbZ_!!630625371-0-wxb1688.jpg_600x600q90.jpg_.webp',
      href: '#'
    }, {
      imgUrl: 'https://img.alicdn.com/imgextra/i4/630625371/O1CN01xtApAL1pXy2qYlFwC_!!630625371-0-wxb1688.jpg_600x600q90.jpg_.webp',
      href: '#'
    }, {
      imgUrl: 'https://img.alicdn.com/imgextra/i4/O1CN01nGJstv1icMWbWJl3a_!!6000000004433-0-tps-600-256.jpg_600x600q90.jpg_.webp',
      href: '#'
    }, {
      imgUrl: 'https://img.alicdn.com/imgextra/i2/O1CN01R92HcE1UXPdlNqjLP_!!6000000002527-2-tps-600-256.png?templateIdFromStarCloud=859&materialIdFromStarCloud=6617',
      href: '#'
    }, {
      imgUrl: 'https://img.alicdn.com/imgextra/i4/O1CN01izCgpX22H6SKSoEgK_!!6000000007094-0-tps-600-256.jpg_600x600q90.jpg_.webp',
      href: '#'
    }]);
    // 新闻内容
    let newsData = reactive([{
      title: '平台公告',
      isActive: true,
      activeClass: 'actived',
      address: '#',
      arr: [{
        name: '阶梯价档数变更意见征集',
        address: '#'
      }, {
        name: '1688行业类目调整通知',
        address: '#'
      }]
    }, {
      title: '最新规则',
      isActive: false,
      activeClass: '',
      address: '#',
      arr: [{
        name: '保价险服务标准(买家版)',
        address: '#'
      }, {
        name: '保价险服务标准',
        address: '#'
      }]
    }, {
      title: '商人社区',
      isActive: false,
      activeClass: '',
      address: '#',
      arr: [{
        name: '疫情之下工厂如何自救?',
        address: '#'
      }, {
        name: '1688中小企业[纾困计划]活动细节与规则',
        address: '#'
      }]
    }]);
    // 获取品类市场元素数组的父节点
    let f_categoryEle = ref();
    // 获取市场详情元素数组
    let f_marketEle = reactive([]);
    // 搜索框内的输入保存
    let keyWord = ref('');
    // 搜索返回框是否显示
    let isShowResults = ref(false);
    // 搜索返回的结果
    let ObjResult = reactive({
      lastKeyWord: '',
      results: []
    });
    // 监视输入框输入，发送ajax请求获取数据
    let timer = null;
    // 最近搜索关键字【数组】
    let recent_keywords = reactive([]);
    // 搜索框的占位符
    let ph = ref(props.ph);
    // 获取最外层盒子
    let outBox = ref();
    // 决定搜索框是否显示
    let isShowSearch = ref(false);

    // 返回提示内容
    function computeTitle(m_obj) {
      let title = '';
      m_obj.markets.forEach(obj => {
        title += obj.name;
      });
      return title;
    }

    // 绑定事件
    function LiMouseEnter(obj) {
      obj.isActive = true; // 切换为活动
    }

    function LiMouseLeave(obj) {
      obj.isActive = false;
    }
    // 新闻鼠标悬浮，显示下划线
    function newsActive(newsObj, e) {
      for (let item of newsData) {
        item.isActive = false;
        item.activeClass = '';
      }
      newsObj.isActive = true;
      newsObj.activeClass = 'actived';
    }
    // 获取市场详情块数组
    function getDetailsPieceArr(el) {
      f_marketEle.push(el);
    }
    let detailsLeft = ref();
    let detailsTop = ref();
    let timer_resize = null; // 防抖
    watch([detailsLeft, detailsTop], newValueArr => {
      if (timer_resize != null) {
        // 定时器不为空时清空定时器
        clearTimeout(timer_resize);
      }
      timer_resize = setTimeout(() => {
        let categoryEle = f_categoryEle.value;
        for (let i = 0; i < categoryEle.children.length; i++) {
          f_marketEle[i].style.left = newValueArr[0] + 'px';
          f_marketEle[i].style.top = categoryEle.children[i].offsetTop + 'px';
        }
        timer_resize = null; // 定时器执行结束置为null
      }, 100);
    });

    // 搜索框点击事件
    function clickInput() {
      if (keyWord.value.length === 0) {
        // 输入框未输入内容
        isShowResults.value = false;
      } else {
        searchData();
      }
    }
    // 立即发送ajax请求【输入框有内容时点击发送ajax请求】
    function searchData() {
      if (timer != null) {
        // console.log('timer != null');
        // 移除定时器
        clearTimeout(timer);
        timer = null;
      }
      // 无关键字，取消发送ajax请求
      if (keyWord.value.length === 0) {
        return;
      }
      instance_Search({
        method: 'GET',
        url: '/Search/searchData',
        params: {
          key: keyWord.value
        }
      }).then(response => {
        // console.log('得到的响应体:', response.data);
        // 保存本次搜索的关键字
        ObjResult.lastKeyWord = keyWord.value;
        // 清空上一次的返回结果
        ObjResult.results = [];
        let keyLength = keyWord.value.length;
        let arr = response.data;
        // 把获取到的数据与关键字相同的前缀部分删去
        arr.forEach(element => {
          ObjResult.results.push(element.slice(keyLength));
        });
        // 显示搜索返回框
        isShowResults.value = true;
      }, reject => {
        // 没有得到对应的数据
        // 隐藏搜索返回框
        isShowResults.value = false;
      });
    }
    // 延迟0.7s发送ajax请求【等待用户输入完全】
    function delaySearchData() {
      // console.log('timer:', timer);
      if (timer != null) {
        // console.log('timer != null');
        // 移除定时器
        clearTimeout(timer);
        timer = null;
      }
      // 无关键字，取消发送ajax请求
      if (keyWord.value.length === 0) {
        return;
      }
      timer = setTimeout(() => {
        // 发送ajax请求
        // console.log('输入:', keyWord.value);
        instance_Search({
          method: 'GET',
          url: '/Search/searchData',
          params: {
            key: keyWord.value
          }
        }).then(response => {
          // console.log('得到的响应体:', response.data);
          // 保存本次搜索的关键字
          ObjResult.lastKeyWord = keyWord.value;
          // 清空上一次的返回结果
          ObjResult.results = [];
          let keyLength = keyWord.value.length;
          let arr = response.data;
          // 把获取到的数据与关键字相同的前缀部分删去
          arr.forEach(element => {
            ObjResult.results.push(element.slice(keyLength));
          });
          // 显示搜索返回框
          isShowResults.value = true;
        }, reject => {
          // 没有得到对应的数据
          // 隐藏搜索返回框
          isShowResults.value = false;
        });
      }, 700);
    }
    // 【搜索按钮点击事件】发送ajax请求，发送关键字(传入要搜索的关键字)
    function sendKeyword(keyword) {
      keyword = keyword.trim(); // 消除左右两侧的空格
      // 输入为空
      if (keyword === '') {
        alert('输入框为空【此处闪烁两次输入框】');
        return;
      }
      // alert('1、发送ajax请求【数据是keyword】,得到新页面后在新页签打开;\n2、将keyword保存到localStorage');
      //【搜索的关键字存储到浏览器中】
      // 判断新关键字在数组中是否已存在
      let isExit = false;
      // 获取本地存储的最近搜索关键字数组
      let recentArr = JSON.parse(localStorage.getItem('Alibar-recentSearch-keyWord')) || [];
      recent_keywords.splice(0); // 删除所有元素
      for (let tmp of recentArr) {
        recent_keywords.push(tmp);
      }
      recent_keywords.forEach(element => {
        if (element === keyword) {
          isExit = true;
        }
      });
      // 已存在，不需要更新
      if (isExit === true) {
        return;
      }
      // 新的关键字补充到【最近搜索】数组内
      recent_keywords.push(keyword);
      // 把新的关键字数组放到本地存储内
      localStorage.setItem('Alibar-recentSearch-keyWord', JSON.stringify(recent_keywords));
    }
    // 关键字点击事件
    function clickKey(keyword) {
      // console.log('关键字点击了!', keyword);
      keyWord.value = keyword;
      sendKeyword(keyword);
    }
    // 【输入框失去焦点】隐藏搜索提示框与搜索结果框
    function hideTip_Result() {
      // console.log('我失去焦点了！');
      isShowResults.value = false;
    }
    // 延时触发失去焦点事件【确保点击事件可以成功执行】
    function delayBlur() {
      setTimeout(() => {
        hideTip_Result();
      }, 150);
    }

    // 【模拟】第一次将鼠标移动到“品类市场”时，增加定时器
    function addTimer_getMessage(n) {
      if (marketLoading_timer[n] === -1) {
        console.log('信息发出');
        // 一秒后接收到消息
        marketLoading_timer[n] = setTimeout(() => {
          console.log('定时器内部：一秒到了，信息返回');
          marketLoading[n] = false;
        }, 3000);
      }
    }

    // 挂载前
    onBeforeMount(() => {
      // 【模拟】设置市场信息为 正在加载  +   初始化市场信息定时器 
      for (let i in marketObj) {
        marketLoading[i] = true;
        marketLoading_timer.push(-1);
      }
      // console.log('初始化的timer:', marketLoading_timer);
    });

    // 生命周期挂载后
    onMounted(() => {
      // 计算市场详情块的坐标
      let categoryEle = f_categoryEle.value;
      detailsLeft.value = categoryEle.offsetLeft + categoryEle.offsetWidth;
      detailsTop.value = categoryEle.offsetTop;
      // window页面缩放事件绑定函数
      function resizeEvent() {
        detailsLeft.value = categoryEle.offsetLeft + categoryEle.offsetWidth;
        detailsTop.value = categoryEle.offsetTop;
      }
      // 页面缩放监视，更新市场详情块坐标
      window.addEventListener('resize', resizeEvent, false);
      // 【添加属性监视】根据isActive属性来决定是否加上activeLi类名
      for (let obj of marketObj) {
        // console.log('【遍历marketObj】obj', obj);
        watch(() => {
          return obj.isActive;
        }, value => {
          // console.log('活动的元素下标是:', marketObj.findIndex((e) => {return e===obj}));
          let index = marketObj.findIndex(e => {
            return e === obj;
          });
          if (value === true) {
            f_categoryEle.value.children[index].className = 'activeLi';
          } else {
            f_categoryEle.value.children[index].className = '';
          }
        });
      }

      // 获取本地存储的最近搜索关键字数组
      let recentArr = JSON.parse(localStorage.getItem('Alibar-recentSearch-keyWord')) || [];
      for (let tmp of recentArr) {
        recent_keywords.push(tmp);
      }

      // console.log('当前最外层盒子的垂直偏移量为：', outBox.value.offsetTop);
      // window滚动事件绑定函数
      function scrollEvent() {
        // console.log('滚动偏移量', window.scrollY);
        if (window.scrollY >= outBox.value.offsetTop) {
          isShowSearch.value = true;
        } else {
          isShowSearch.value = false;
        }
      }
      // 绑定滚动事件，判断是否显示搜索框
      window.addEventListener('scroll', scrollEvent, false);
    });
    onUnmounted(() => {
      //解绑window事件
      window.removeEventListener('resize', resizeEvent, false);
      window.removeEventListener('scroll', scrollEvent, false);
    });
    return {
      // 属性
      marketObj,
      // 品类市场
      carouselData,
      // 轮播图图片与跳转链接
      newsData,
      // 新闻内容
      f_categoryEle,
      // 品类市场元素数组的父节点
      f_marketEle,
      // 市场详情元素数组的父节点
      ...toRefs(ObjResult),
      // 搜索返回的结果
      keyWord,
      // 关键字  
      isShowResults,
      // 是否显示搜索返回框 
      ph,
      // 搜索框占位符
      outBox,
      // 最外层盒子
      isShowSearch,
      // 决定搜索框显示的变量
      marketLoading,
      // 【模拟】判断品类市场信息是否返回
      addTimer_getMessage,
      // 【模拟】增加品类市场信息接收定时器，模拟信息送达

      // 计算属性：
      computeTitle,
      // 左侧盒子悬停提示内容

      // 绑定事件
      LiMouseEnter,
      // 左侧盒子小li鼠标移入事件
      LiMouseLeave,
      // 左侧盒子小li鼠标移出事件
      newsActive,
      // 新闻鼠标悬浮，显示下划线

      getDetailsPieceArr,
      // 获取市场详情块数组

      clickInput,
      // 搜索栏点击事件
      searchData,
      // 立即发送ajax请求查找数据
      delaySearchData,
      // 延迟发送ajax请求查找数据
      sendKeyword,
      // 发送关键字事件  
      clickKey,
      // 关键字点击事件
      delayBlur // 延迟失去焦点事件
    };
  }
};