import { reactive, ref, toRefs } from '@vue/reactivity';
import { computed, onBeforeMount, onMounted, onUnmounted } from '@vue/runtime-core';
export default {
  name: 'Sidebar',
  setup(props) {
    // 最右侧固定的大侧边栏内的相关按钮是否活动
    let bigSizeBar = reactive({
      mine: false,
      // 我
      invite: false,
      // 邀约
      update: false,
      // 上新
      track: false,
      // 足迹
      message: false,
      // 消息
      headline: false,
      // 头条
      big_goTop: false // 置顶
    });
    // 为了保留字体位置，使用visibility进行隐藏与显示，属性均为计算属性
    let bigSizebar_p_visibility = reactive({});
    // 最右侧大侧边栏【用于得到元素】
    let ele_bigSizeBar = ref();
    // ul的top偏移量
    let ul_top = ref();
    // 置顶定时器
    let goTop_timer = null;
    // 小侧边栏：按钮是否活动
    let smallSizeBar = reactive({
      replenishment: false,
      // 快速补货
      cardCenter: false,
      // 卡券中心
      purchaseList: false,
      // 进货单
      feedback: false,
      // 反馈
      plus: false,
      // 会员中心
      small_goTop: false // 置顶按钮
    });
    // 小侧边栏: 置顶那妞是否显示
    let small_isShowGoTop = ref();

    // 计算ul的top值
    function get_ul_top() {
      let h = document.documentElement.clientHeight; // 获取可视区高度，即100vh
      if (h >= 753) {
        // 经过手动查看，ul的高度是407px,    top为(100vh-407)/2向上取整
        ul_top.value = Math.ceil((h - 407) / 2).toString() + 'px';
      } else if (h < 753 && h >= 573) {
        //当100vh为573px时，top为50px，刚好与“顶按钮和ul之间的间距”相等
        // top为 （100vh - 置顶按钮偏移量 - 置顶按钮高度 - ul高度）/2 【平分剩余空间给 top 与 置顶按钮和ul之间的间距】
        // console.log('计算出来的值', (h-50-16-407)/2);
        ul_top.value = Math.ceil((h - 50 - 16 - 407) / 2).toString() + 'px';
      } else {
        // 将bigSizebar的最小高度设置为543px = 20   + 407     +   50    +       16    +     50
        //                                   top  + ul高度   +  间距   + 置顶按钮高度 + 置顶按钮底部偏移
        ul_top.value = h - 407 - 50 - 16 - 50;
        if (ul_top.value < 20) {
          ul_top.value = 20;
        }
        ul_top.value += 'px';
      }
    }
    onBeforeMount(() => {
      // console.log('可视区高度:', document.documentElement.clientHeight);
      get_ul_top(); // 计算bigSizebar的ul的偏移量
      // console.log('挂载前的ul_top:', ul_top.value);
      // 给bigSizebar_p_visibility添加属性
      for (let i in bigSizeBar) {
        bigSizebar_p_visibility[i] = computed(() => {
          return bigSizeBar[i] ? 'visible' : 'hidden';
        });
      }
      console.log('挂载前的页面滚动距离:', document.documentElement.scrollTop);
      if (document.documentElement.scrollTop === 0) {
        console.log('挂载前发现页面未滚动');
        small_isShowGoTop.value = false;
      }
      // console.log('1111');
      // console.log(bigSizebar_p_visibility);
      // for(let i in bigSizebar_p_visibility) {
      //     console.log(i, bigSizebar_p_visibility[i]);
      // }
    });

    onMounted(() => {
      // console.log('元素:', ele_bigSizeBar.value.offsetHeight);
      // 窗口重绘事件的定时器
      let bigSizebar_timer = null;
      function set_ul_top() {
        // 存在上一个定时器
        if (bigSizebar_timer != null) {
          clearTimeout(bigSizebar_timer);
          // console.log("清除了上一个定时器呢");
        }

        bigSizebar_timer = setTimeout(() => {
          get_ul_top();
          bigSizebar_timer = null; // 定时器结束时，把bigSizebar_timer设置为null
          // console.log('窗口改变的任务结束咯');
        }, 500);
      }
      // 窗口重绘事件：调整ul的位置
      window.addEventListener('resize', set_ul_top, false);
      function set_smallSizeBar_goTop() {
        if (small_isShowGoTop.value === false && document.documentElement.scrollTop > 0) {
          small_isShowGoTop.value = true;
        } else if (small_isShowGoTop.value === true && document.documentElement.scrollTop === 0) {
          small_isShowGoTop.value = false;
        }
      }
      // 浏览器滚动事件: 判断是否显示小测边框的置顶按钮
      window.addEventListener('scroll', set_smallSizeBar_goTop, false);
    });

    // bigSizebar：鼠标悬浮修改元素为活动状态
    function big_mouseenter(name) {
      bigSizeBar[name] = true;
    }
    // bigSizebar：鼠标离开修改元素为未活动
    function big_mouseleave(name) {
      bigSizeBar[name] = false;
    }
    // smallSizeBar：鼠标悬浮修改元素为活动状态
    function small_mouseenter(name) {
      smallSizeBar[name] = true;
    }
    // smallSizebar：鼠标离开修改元素为未活动
    function small_mouseleave(name) {
      smallSizeBar[name] = false;
    }
    // 置顶按钮: 调用函数
    function click_goToTop() {
      // 定时器不为空，说明正在移动
      if (goTop_timer != null) {
        return;
      }
      let h = document.documentElement.scrollTop; // 获取页面的滚动距离
      // console.log('滚动距离是:', h);
      if (h === 0) {
        // 页面处于顶端，不需要移动
        return;
      }
      let onceStep = Math.ceil(h / 30); // 0.3s内完成动画，每次移动间隔0.01s，要分成0.3/0.01 = 30
      // console.log('单次步长:', onceStep);
      goTop_timer = setInterval(() => {
        if (h === 0) {
          clearInterval(goTop_timer);
          goTop_timer = null;
          return;
        }
        h -= onceStep;
        h = h < 0 ? 0 : h;
        window.scroll(0, h);
        // console.log('移动...');
      }, 10);
    }
    onUnmounted(() => {
      // 卸载组件时应当清除window绑定的事件
      window.removeEventListener('resize', set_ul_top, false);
      window.removeEventListener('scroll', set_smallSizeBar_goTop, false);
    });
    return {
      ...toRefs(bigSizeBar),
      // 最右侧固定的大侧边栏内的相关按钮是否活动
      bigSizebar_p_visibility,
      // 为了保留字体位置，使用visibility进行隐藏与显示，属性均为计算属性
      ele_bigSizeBar,
      // 最右侧大侧边栏【用于得到元素】
      ul_top,
      // ul的top偏移量
      big_mouseenter,
      big_mouseleave,
      click_goToTop,
      ...toRefs(smallSizeBar),
      // 小侧边栏：按钮是否活动 
      small_isShowGoTop,
      // 小侧边栏: 置顶那妞是否显示
      small_mouseenter,
      small_mouseleave
    };
  }
};